import { CInput, CTextArea } from "@components";
import {
  Col,
  Empty,
  Form,
  Row,
  Collapse,
  Card,
  Select,
  Table,
  Input,
  Tag,
  InputNumber,
} from "antd";
import React from "react";

const CNumbering = ({
  isForms,
  isEmpty,
  handleChange,
  forms,
  errors,
  onChange1,
  onChangeSegment1,
  onChangeValue1,
  onChange2,
  onChangeSegment2,
  onChangeValue2,
  onChange3,
  rowSelection,
  formsTable,
  valueListMaster,
  listMaster,
  scopeCode,
  masterData = [],
  valueData = [],
}) => {
  const { Panel } = Collapse;

  const formItemLayout = {
    labelCol: {
      xs: { span: 20 },
      sm: { span: 4 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 20 },
    },
  };
  const Segment = (key) => {
    if (key == 1) {
      return formsTable.segment1 != "" ? formsTable.segment1 : null;
    } else if (key == 2) {
      return formsTable.segment2 != "" ? formsTable.segment2 : null;
    } else if (key == 3) {
      return formsTable.segment3 != "" ? formsTable.segment3 : null;
    }
  };

  const columns = [
    {
      title: "Segment",
      width: 250,
      render: (item) =>
        Array.isArray(item.segment) ? (
          <Select
            showSearch
            placeholder="Select segment"
            optionFilterProp="children"
            onChange={item.onChangeSegment}
            style={{ width: "100%" }}
            value={Segment(item.key)}
            filterOption={(input, option) =>
              (option?.label ?? "").toLowerCase().includes(input.toLowerCase())
            }
            options={item.segment}
          />
        ) : (
          item.segment
        ),
    },
    {
      title: "Value",
      render: (item) =>
        item.key == 1 ? (
          <Select
            showSearch
            placeholder="Value"
            optionFilterProp="children"
            value={formsTable.value1 != "" ? formsTable.value1 : null}
            style={{ width: "100%" }}
            onChange={item.onChangeValue}
            filterOption={(input, option) =>
              (option?.label ?? "").toLowerCase().includes(input.toLowerCase())
            }
            options={listMaster}
          />
        ) : item.key === 2 ? (
          <Input
            placeholder="Value"
            value={
              formsTable.segment2 == "month-year" ? "MMYYYY" : formsTable.value2
            }
            onChange={item.onChangeValue}
            disabled={formsTable.segment2 == "month-year"}
          />
        ) : (
          item.value
        ),
    },
    {
      title: "Length",
      width: 125,
      render: (item) => (
        <InputNumber
          placeholder="Lenght"
          value={
            item.key == 2
              ? formsTable.segment2 == "month-year"
                ? ""
                : formsTable.length2
              : item.key == 1
              ? formsTable.length1
              : formsTable.length3
          }
          onChange={item.onChange}
          style={{ width: "100%" }}
          min={0}
          disabled={
            item.key == 2
              ? formsTable.segment2 == "month-year"
                ? true
                : false
              : false
          }
        />
      ),
    },
  ];

  const columnsUsage = [
    {
      title: "Form Usage",
      dataIndex: "name",
      width: 200,
      render: (value, row, index) => {
        const obj = {
          children: value,
          props: {},
        };
        if (index % 3 === 0) {
          obj.props.rowSpan = 3;
        }
        // These two are merged into above cell
        if (index % 3 === 1) {
          obj.props.rowSpan = 0;
        }
        if (index % 3 === 2) {
          obj.props.rowSpan = 0;
        }
        return obj;
      },
    },
    {
      title: "Detail",
      dataIndex: "age",
      width: 200,
    },
    {
      title: "Next Number",
      dataIndex: "address",
      width: 200,
      render: (r) => <Tag color="cyan">{r}</Tag>,
    },
  ];

  const data = [
    {
      key: 1,
      onChangeSegment: onChangeSegment1,
      onChangeValue: onChangeValue1,
      onChange: onChange1,
      segment: masterData,
      value: valueListMaster,
    },
    {
      key: 2,
      onChangeSegment: onChangeSegment2,
      onChangeValue: onChangeValue2,
      onChange: onChange2,
      segment: valueData,
    },
    {
      key: 3,
      value: "#########",
      segment: "Running Number",
      onChange: onChange3,
    },
  ];

  const dataUsage = [
    {
      key: 0,
      name: `Asset Group`,
      age: `BUILDINGS`,
      address: `C01112384`,
    },
    {
      key: 1,
      name: `Asset Group`,
      age: `COMP`,
      address: `C01112385`,
    },
    {
      key: 2,
      name: `Asset Group`,
      age: `OFFICE`,
      address: `C01112386`,
    },
    {
      key: 0,
      name: `Asset`,
      age: `BUILDINGS`,
      address: `C01112384`,
    },
    {
      key: 1,
      name: `Asset`,
      age: `COMP`,
      address: `C01112385`,
    },
    {
      key: 2,
      name: `Asset`,
      age: `OFFICE`,
      address: `C01112386`,
    },
  ];

  return (
    <>
      {isEmpty && <Empty />}
      {isForms && (
        <>
          <Row>
            <Col span={24}>
              <Card title="Header" bordered={false}>
                <Form {...formItemLayout}>
                  <CInput
                    label="Code"
                    validate={errors.code}
                    name="code"
                    id="code"
                    value={forms.code}
                    onChange={handleChange}
                  />
                  <CInput
                    value={forms.name}
                    label="Name"
                    validate={errors.name}
                    name="name"
                    id="name"
                    isRequired
                    onChange={handleChange}
                    style={{ marginTop: -20 }}
                  />
                  <CTextArea
                    value={forms.description}
                    label="Description"
                    validate={errors.description}
                    name="description"
                    id="description"
                    onChange={handleChange}
                    style={{ marginTop: -20 }}
                  />
                </Form>
              </Card>
            </Col>
            <Col span={24} style={{ paddingTop: 10 }}>
              <Collapse defaultActiveKey={["0"]} style={{ marginTop: 10 }}>
                <Panel
                  header={
                    <Row>
                      <Col span={12}>Scope Parameter</Col>
                      <Col span={12}>
                        {scopeCode && (
                          <Tag
                            color="cyan"
                            style={{ fontSize: 16, float: "right" }}
                          >
                            {scopeCode}
                          </Tag>
                        )}
                      </Col>
                    </Row>
                  }
                  key="1"
                >
                  <div>
                    <Table
                      rowSelection={rowSelection}
                      columns={columns}
                      dataSource={data}
                      pagination={false}
                    />
                  </div>
                </Panel>
              </Collapse>
              <Collapse defaultActiveKey={["0"]} style={{ marginTop: 10 }}>
                <Panel header="Number Usage List" key="1">
                  <div>
                    <Table columns={columnsUsage} dataSource={dataUsage} />
                  </div>
                </Panel>
              </Collapse>
            </Col>
          </Row>
        </>
      )}
    </>
  );
};

export default CNumbering;
